import { useStaticQuery, graphql } from "gatsby";

type Post = {
  id: string;
  html: string;
  frontmatter: {
    title: string;
  };
};
export const usePosts = () => {
  const posts = useStaticQuery(
    graphql`
      {
        allMarkdownRemark {
          edges {
            node {
              id
              html
              frontmatter {
                title
              }
            }
          }
        }
      }
    `
  );
  return posts.allMarkdownRemark.edges.map((edge: any) => edge.node) as Post[];
};
